.App {
  text-align: center;
  background-color: #152c5f;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  color: white;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#profilePic
{
  border-radius: 50%;
}

#StackCont
{
  background-color: whitesmoke;
  border-radius: 5%;
  
  
}

#welcome
{
  height: 100vh;
  background-color: rgb(15, 19, 51);
  color : whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
}

.sub_div {
  left: 48%;
  position: absolute;
  bottom: 0px;
}

.scrollD
{
  animation: MoveUpDown 2s linear infinite;
  position: absolute;
  left: 0;
  bottom: 0;
}

@keyframes MoveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 40px;
  }
}

#icons:hover
{
  transform : scale(1.1);
}

